.navbar{
    display: flex;
    align-items: center;
    justify-content: space-between;
    /* margin: 20px 10px; */
    padding-top: 5px;
    padding-bottom: 5px;
    background: #161513;
}
.logo{
    margin-left: 25px;
}
.nav-menu{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 60px;
    font-size: 20px;
}
.nav-connect{
    padding: 13px 20px;
    margin-right: 25px;
    border-radius: 50px;
    background: linear-gradient(267deg, #DA7C25 0.36%, #B923E1 102.06%);
    font-size: 20px;
    cursor: pointer;
    transition: 0.4s;
}
.nav-connect:hover{
  transform: scale(1.05);
}
.nav-menu li {
    display: flex;
    flex-direction: column;
    gap: 5px;
    cursor: pointer;
}
.nav-menu li img{
    display: flex;
    margin: auto;
}
.anchor-link{
    text-decoration: none;
    color: #ffff;
}
.nav-mob-close{
    display: none;
}
.nav-mob-open{
    display: none;
}
.navbar.fixed{
    background: #161513;
    transition: 0.3s ease-in-out;
    position: sticky;
    top: 0;
    z-index: 50;
}
@media (max-width:768px){
    .navbar{
        margin: 20px 50px;
    }
    .nav-connect{
        display: none;
    }
    .nav-mob-open{
        display: block;
        position: fixed;
        right: 30px;
    }
    .nav-mob-close{
        display: block;
        position: relative;
        top: 30px;
        left: 290px;
        width: 30px;
    }
    .navbar.fixed{
        position: static;
        /* display: none; */
    }
    .nav-menu {
        position: fixed;
        flex-direction: column;
        align-items: start;
        top: 0;
        right: -350px;
        gap: 30px;
        background-color: #1F0016;
        width: 350px;
        height: 100%;
        z-index: 2;
        transition: right 0.5s;
    }
    .nav-menu li{
        font-size: 25px;
        padding-left: 100px;
        flex-direction: row;
        gap: 20px;
    }
}